.theme--esters #main .products {
	letter-spacing: 1px;
	text-transform: uppercase;
	text-align: center;
}
.theme--esters #main .woocommerce-loop-product__title {
	letter-spacing: 2px;
	margin: 0.5em 0 0.2em;
}
.theme--esters #main ul.products .btn {
	font-size: 0.75em;
	margin-top: 0.6em;
}

.product-listing .woocommerce-pagination {
	text-align: center;
}

.product-listing #primary {
	background-color: white;
	border: 1px solid currentColor;
	padding: 3.5%;
	margin: 0 auto;
	max-width: 1140px;
}
.product-listing #main {
	margin: 0;
}
@media (min-width: 940px) {
	.product-listing #primary {
		padding: 1.7rem;
		margin-top: calc((100vw - 940px) * 0.5);
		margin-bottom: calc((100vw - 940px) * 0.5);
	}
}
@media (min-width: 1060px) {
	.product-listing #primary {
		margin-top: 60px;
		margin-bottom: 60px;
	}
}

.product-listing #main .products {
	font-size: 5vw;
}
@media (min-width: 380px) {
	.product-listing #main .products {
		font-size: 0.85em;
	}
}
.product-listing #main .product {
	margin-bottom: 2em;
}

.product-listing #main .products img {
	width: 100%;
}
.product-listing #main .woocommerce-loop-product__title {
	font-size: 0.85em;
}
